body {
  margin: 0;
  padding: 0;
  /*font-family: sans-serif;*/
  font-family: "Arial";
  overflow: hidden;
  height: 100%;
  width: 100%;
  background-color: black !important;
}

/* #earth_div div{
  visibility: hidden;
} */

.form-group{
  margin-right:1em;
}

i.icon{
  font-size:30px;
}

.flex-container {
  padding: 0;
  margin: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  
  -ms-flex-flow: row wrap;
  
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  position:relative;
  top:0;
  left:0;
}

.flex-item {
  -ms-flex: 1 0 21%;
      flex: 1 0 21%;
  background: transparent;
  margin:0;
  
  color: transparent;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.positions{ display:none }

.debug .positions{ 
  display: inherit; 
  position: absolute; 
  left: 10px; 
  top: 10px;
  color: white;
}

.debug .flex-item.selected {
  background: green;
}

.debug .flex-item {
  border:1px dashed tomato;
}

.thermometer-box{
  position: absolute;
  top: 30px;
  right: 10px;
  text-align: center;
}

.thermometer-box > div{
  color: white;
  font-size: 2rem;
}

.thermometer-box > button{
  margin-top: 20px;
}

.thermometer__percent-current{
  display: none;
}

#end{
  color: white !important;
  text-align: center;
  padding: 20px;
  height: 100vh;
  overflow-y: auto;
}

#end h1{
  color: white !important; 
  margin-bottom: 20px;
}

#end table{
  margin: auto;
  border: none;
  border-collapse: collapse;
}

#end table td, #end table th{
  padding: 5px 10px;
}



